@font-face {
  font-family: 'Handlee';
  src: url('../resources/fonts/Handlee/Handlee-Regular.ttf')  format('truetype'), /* Safari, Android, iOS */
}

@font-face {
  font-family: 'Comfortaa';
  src: url('../resources/fonts/Comfortaa/Comfortaa-VariableFont:wght.ttf')  format('truetype'), /* Safari, Android, iOS */
}

h1 {
  font-family: 'Comfortaa', 'sans-serif';  
  color: #7da2ff;
  margin: 0;
}

h2,h3 {
  font-family: 'Handlee', 'sans-serif';  
  color: #7f9add;
  margin: 0;
}


.Hero{
  @extend .flex-center;
  background-size: cover;
  background-position-y: 40%;
  height: 100vh;
  width: 100%;

  video{
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    max-width: 1920px;
    filter: brightness(1.2);
  }

  .Logo{
    position: absolute;

  }
}

.Content{
  position:relative;
  display: flex;
  height: 50vh;
  opacity: 0;
  animation-delay: 1.5s;
  animation-name: emerge;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  

  .Entry{
    @extend .flex-center;

    top: -25vh;
    left: calc(50% - 25vw);
    position: absolute;
    width: 50vw;
    height: 20vh;
    background: linear-gradient(#e2faffb9, #ffffff);
    border-radius: 20px;
    flex-direction: column;

    
  }
}

.inPage{
  @extend .flex-center;

  width: 50vw;
  background: linear-gradient(#ffffffb9, #ffffff);
  border-radius: 20px;
  flex-direction: column;
  padding: 1em;


  div{
    @extend .flex-center;
    width: 80%;
    
    input{
      width: 40%;
      font-size: .9rem;
      padding: 1rem;
      border-radius: 10px;
      border: none;
      margin-top: 1em;
    }
  }
}

.Icon{
  max-width: 35px;
  margin-left: 20px;
  margin-top: 20px;
}

.flex-center{
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes emerge {
  from {
    opacity: 0;
    transform: translate3d(0,10%,0);
  }
  to {
    opacity: 1;
    transform: translate3d(0,0,0);
  }
}
